
.svn-projects
  .project(v-for="source in sources" :key="source.url"
      @click='openProjectPage(source)'
      @click.middle='openProjectTab(source)'
      @click.meta='openProjectTab(source)'
      @click.ctrl='openProjectTab(source)'
      )
      .desc
        h3 {{ source.name }}
        p {{ source.description }}

